<template>
	<div>
		<van-nav-bar
		  fixed
		  border
		  :title="titles"
		  left-arrow
		  @click-left="goBack"
		/>
		<van-tabs sticky v-model="active" :swipeable="true" @change="changeTab">
		  <van-tab title="消费记录">
			 <van-row type="flex"  justify="space-around" v-for="item in data" :key="item.id">
			    <van-col span="22">
					<div class="box_consum">
						<div>
							<p class="p1">{{item.title}}</p> 
							<p class="p2">{{item.date}}</p>
						</div>
						<div class="p3">￥{{item.price}}</div>
					</div>
				</van-col>
			  </van-row>
			  <div class="no-content" v-if="data.length == 0">
			    <img src="../assets/no-content.png" alt="" />
			    <p><strong>暂无消费记录</strong></p>
			    <p>暂无消费记录，快去看看吧~</p>
			  </div>
		  </van-tab>
		  <van-tab title="提现记录">
			  <van-row type="flex" justify="space-around">
			    <van-col span="22">
					<!-- <div class="box_consum">
							<div>
								<p class="p1">积分提现</p> 
								<p class="p2">2020-2-28</p>
							</div>
							<div class="p3">￥188.00</div>
						</div> -->
					</van-col>
			  </van-row>
			  <div class="no-content">
			    <img src="../assets/no-content.png" alt="" />
			    <p><strong>暂无提现记录</strong></p>
			    <p>暂无提现记录，快去看看吧~</p>
			  </div>
		  </van-tab>
		</van-tabs>
	</div>
</template>

<script>
import {consumeRecords}	 from "@/api/v4.js"
import {
  Button,
  Row,
  Col,
  NavBar,
  List,
  Cell,
  PullRefresh,
  Tabs,
  Tab
} from "vant";
export default{
	components: {
	  [Tabs.name]: Tabs,
	  [Tab.name]: Tab,
	  [Row.name]: Row,
	  [Col.name]: Col,
	  [List.name]: List,
	  [Cell.name]: Cell,
	  [PullRefresh.name]: PullRefresh,
	  [NavBar.name]: NavBar,
	  [Button.name]: Button,
	},
	data(){
		return{
			active: 0,
			titles:"消费记录",
			data:[]
		}
	},
	created() {
		consumeRecords().then(res=>{
			this.data = res
		})
	},
	methods:{
		goBack() {
		  if (this.from) {
		    this.$router.back();
		  } else {
		    this.$router.replace("/demands");
		  }
		},
		changeTab(name,title){
			this.titles =title
		},
	}
}
</script>

<style scoped lang="less">
.box_consum{
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom:1px solid #ebedf0;
	.p1{
		color: #333;
		margin-bottom: 5px;
	}
	.p2{
		color: #999999;
		font-size: 12px;
		margin-top: 0;
	}
	.p3{
		color: #333;
		font-size: 14px;
	}
}	
@media (min-width:750px) {
	.box_consum{
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom:1px solid #ebedf0;
		.p1{
			color: #333;
			margin-bottom: 5px;
		}
		.p2{
			color: #999999;
			font-size: 12px;
			margin-top: 0;
		}
		.p3{
			color: #333;
			font-size: 14px;
		}
	}	
}
</style>